<template>
  <div style="background: #f6f6f6; min-height: 680px;">
    <van-sticky>
      <div class="topMost">
        <div class="topBody">
          <van-checkbox
            v-model="checkAll"
            shape="square"
            checked-color="#f00"
            icon-size="16px"
            @click="selectClickAll"
            ><span style="color: #666">选择开票订单</span></van-checkbox
          >
          <p>已选择订单金额 <span>{{amount}}</span> 元</p>
        </div>
      </div>
    </van-sticky>
    <div class="orderMsg">
      <!-- 下面填写v-for -->
      <div class="onceWares" v-for="item in data" :key="item.id">
        <div class="topMsg">
          <p>订单号：{{item.orderSn}}</p>
          <p class="timer">{{item.createTime}}</p>
        </div>
        <div class="waresName">
          <van-checkbox v-model="item.visible" shape="square" checked-color="#f00">
            <div class="waresBody">
              <div class="wareImage">
                <img :src="item.orderItemList[0] && item.orderItemList[0].productPic" alt="" />
              </div>
              <div class="wareTitle">
                <div class="title">{{item.orderItemList[0] && item.orderItemList[0].productName}}</div>
                <div class="deploy" v-if="item.orderItemList[0] && item.orderItemList[0].productAttr">
                  <p v-if="JSON.parse(item.orderItemList[0].productAttr)[0]">{{JSON.parse(item.orderItemList[0].productAttr)[0].value}}</p>
                  <p v-if="JSON.parse(item.orderItemList[0].productAttr)[1]">{{JSON.parse(item.orderItemList[0].productAttr)[1].value}}</p>
                </div>
                <div class="money">￥<span>{{item.payAmount}}</span></div>
              </div>
            </div>
          </van-checkbox>
        </div>
      </div>
      <div class="confirmBtn">确定</div>
    </div>
  </div>
</template>

<script>

import * as api from '@/data/api/market/index'

export default {
  data() {
    return {
      checkAll: false,
      params: {
        pageNum: 1,
        pageSize: 3,
        total: 0,
        invoiced: false,
        status: -1
      },
      first: 5000,
      amount: 0,
      data: []
    };
  },
  // filters:{
  //   dateFilter: function (data, format = "") {
  //     if (data) {
  //       var dt = new Date(data);
  //       var y = dt.getFullYear();
  //       var m = (dt.getMonth()+1).toString().padStart(2,"0");
  //       var d = dt.getDate().toString().padStart(2,"0");
  //       var h = dt.getHours().toString().padStart(2,"0");
  //       var mm = dt.getMinutes().toString().padStart(2,"0");
  //       var ss = dt.getSeconds().toString().padStart(2,"0");;  
  //       return `${y}-${m}-${d} ${h}:${mm}:${ss}`;
  //     } else {
  //       return '-'
  //     }
  //   }
  // },
  mounted() {
    this.orderList()
    this.amount = this.first
  },
  methods: {
    orderList () {
      let me = this
      api.getOrderList(me.params).then(res => {
        if (res.code === 200) {
          if (res.data.list) {
            me.params.total = res.data.total
            me.params.pageSize = res.data.pageSize
            me.params.pageNum = res.data.pageNum
            res.data.list = res.data.list.map(item => {
              item.visible = false
              return item
            })
            me.data = res.data.list
          }
        } else {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      }).catch((res) => {
        me.$notify({
          message: res.message,
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }).finally(() => {
      })
    },
    selectClickAll () {
      let me =this
      if (me.checkAll) {
        me.data = me.data.map(item => {
          item.visible = true
          me.amount = me.amount + item.payAmount
          return item
        })
      } else {
        me.data = me.data.map(item => {
          item.visible = false
          return item
        })
        me.amount = me.first
      }
    }
  },
};
</script>

<style lang="stylus" scoped>
.topMost {
  width: 100%;
  height: 44px;
  background: #FFFFFF;

  .topBody {
    margin: 0px 17px;
    width: 341px;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    p {
      font-size: 13px;
      color: #666666;

      span {
        color: #111;
      }
    }
  }
}

.orderMsg {
  padding: 1px 0px 33px;
  width: 375px;

  .onceWares {
    margin: 10px 10px 0px;
    width: 355px;
    height: 180px;
    background: #FFFFFF;
    border-radius: 12px;

    .topMsg {
      width: 100%;
      height: 48px;
      border-bottom: 1px solid #f0f0f0;
      display: flex;
      justify-content: space-between;
      align-items: center;

      p {
        margin: 0px 10px;
        color: #444;
        font-size: 13px;
      }

      .timer {
        font-size: 12px;
        color: #999999;
      }
    }

    .waresName {
      padding: 0px 15px;
      width: 325px;
      height: 132px;
      display: flex;
      align-items: center;

      .waresBody {
        width: 300px;
        height: 86px;
        display: flex;
        justify-content: space-between;
        .wareImage img{
          height: 80px;
        }
        .wareTitle {
          width: 204px;

          .title {
            height: 38px;
            font-size: 14px;
            color: #000000;
            font-weight: bold;
            overflow: hidden;
            /* white-space: nowrap; */
            text-overflow: ellipsis;
          }

          .deploy {
            margin-top: 2px;
            width: 60px;
            height: 12px;
            font-size: 12px;
            color: #888888;
            display: flex;
            justify-content: space-between;
          }

          .money {
            margin-top: 15px;
            width: 87px;
            font-size: 15px;
            color: #FF2F2F;

            span {
              font-size: 21px;
              font-weight: bold;
            }
          }
        }
      }
    }
  }

  .confirmBtn {
    width: 345px;
    height: 48px;
    background: #FF4C4C;
    border-radius: 4px;
    margin: 33px 15px 0px;
    font-size: 17px;
    font-weight: bold;
    color: #FFFFFF;
    text-align: center;
    line-height: 48px;
  }
}
</style>


